ks-order-inpost .content {
    background-color: #e8e8e8;
    position: relative;
    box-sizing: border-box;
    margin: 20px;
    max-width: 1400px;
    width: 100%;
    max-height: 100%;
    overflow-y: auto;
}

@media screen and (max-width: 640px) {
    ks-order-inpost .content {
        margin: 0px;
    }
}

ks-order-inpost > ks-button {
    max-width: 300px;
    height: 46px;
    margin: 10px auto 0 auto;
}

ks-order-inpost .topBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #ffcd00;
    height: 46px;
}

ks-order-inpost .missing {
    color: var(--color-secondary);
    margin-bottom: 10px;
}

ks-order-inpost .topBar ks-img2 {
    height: 40px;
}

ks-order-inpost .map {
    height: calc(100dvh - 46px);
    max-height: 900px;
    overflow-x: hidden;
}

@media screen and (max-width: 640px) {
    ks-order-inpost .map {
        max-height: 100%;
    }
}
