import { Component, h, Prop, Element, State, Method } from '@stencil/core';

@Component({
  tag: 'ks-order-inpost',
  styleUrl: 'orderInpost.css'
})
export class OrderInpost {
    @Element() root: HTMLElement;
    @Prop() token: string = "";
    @Prop() api: string = "";
    @Prop() button: string = "";
    @Prop() search: string = "Wrocław, Polska";
    @Prop() phone: string = "";
    
    @Prop() test: boolean = false;

    @Prop() pointMessage: string = "";
    @Prop() missingMessage: string = "";
    @Prop() invalidPhoneMessage: string = "";
    @Prop() editAddressLink: string = "";

    @State() address: string = "";
    @State() code: string = "";
    @State() initialized = false;
    @State() validPhone = true;
    @State() validCode = true;
    @State() valid: boolean = true;

    widget: any;
    overlay: HTMLKsOverlayElement;

    componentDidLoad() {
        this.overlay = this.root.querySelector("ks-overlay");
    }

    componentWillLoad() {
        this.Validate(true);
    }

    show(event: Event) {
        event.preventDefault();
        this.overlay.show();
        this.initialized = true;
    }

    hide(event?: Event) {
        event?.preventDefault();
        this.overlay.hide();
        this.Validate();
    }


    @Method()
    async Validate(initial = false) {
        if(/^[+]*(?:48)*\s*(?:45|50|51|53|57|60|66|69|72|73|78|79|88)(?:(?:[0-9]{7})|(?:[0-9]\s[0-9]{3}\s[0-9]{3}))$/.test(this.phone) === false) {
            this.valid = false;
            this.validPhone = false;
            return;
        }

        if(this.code === "") {
            this.valid = false;
            if(!initial) this.validCode = false; // give a chance before yelling at the user
            return;
        }

        this.valid = true;
        this.validPhone = true;
        this.validCode = true;
    }

    @Method()
    async IsValid() {
        return this.valid;
    }

    @Method()
        async select(point: string, address: string) {
            this.code = point;
            this.address = address;
            this.Validate();
            this.hide(null);
    
            let formData = new FormData();
                formData.append("rodzaj", "wysylka_inpost");
                formData.append("koszyk", "0");
                formData.append("value", address);
                formData.append("punktodbioru", point);
            
            var headers = new Headers();
                headers.append('pragma', 'no-cache');
                headers.append('cache-control', 'no-cache');
            
            fetch(this.api, {
                method: 'POST', 
                headers: headers, 
                body: formData,
                credentials: "same-origin"
            });
        }

    render() {
        const endpoint = this.test ?
            "https://sandbox-easy-geowidget-sdk.easypack24.net" :
            "https://geowidget.inpost.pl"

        return [
            this.code ? <p>{this.pointMessage}: {this.code}</p> : null,
            !this.validCode && <p class="missing">{this.missingMessage}</p>,
            !this.validPhone && <p class="missing">{this.invalidPhoneMessage} {this.phone && `Podany nr: ${this.phone}`}</p>,

            this.validPhone ? 
                <ks-button round icon="map-pin" secondary={!this.validCode} onClick={(e)=>this.show(e)} name={this.button}/> :
                <ks-button round icon="edit" secondary={true} link={this.editAddressLink} name={"Edytuj dane wysyłki"}/>,
                    
            <input type="hidden" name="lokalizacjaRuch" value={this.code}/>,
            <input type="hidden" name="ShippingDestinationCode" value={this.code}/>,

            <ks-overlay dark>
                <div class="content">
                    <div class="topBar">
                        <ks-img2 src="res/cart/inpost-dark.svg" vertical height={100} width={300}/>
                        <ks-button transparent icon="x" onClick={(e)=>this.hide(e)} />
                    </div>
                    {this.initialized && [
                        <link rel="stylesheet" href={`${endpoint}/inpost-geowidget.css`}/>,
                        <script src={`${endpoint}/inpost-geowidget.js`} defer></script>,

                        <script type="text/javascript" innerHTML="
                            document.addEventListener('onpointselect', (event) => {
                                console.log(event)
                                document.querySelector('ks-order-inpost').select(event.detail.name, event.detail.address.line1 + ' ' + event.detail.address.line2)
                            });
                        "></script>,
                        
                        <div class="map" innerHTML={`<inpost-geowidget onpoint="onpointselect" token='${this.token}' language='pl' config='parcelcollect'></inpost-geowidget>`} />
                    ]}
                </div>
            </ks-overlay>
        ];
    }
}